/*profile*/

/*be careful with classnames in css as having the same will affect other elements even if they're in another file*/

.container1-profile{
    height: 100vh;
    width: 100vw;

    margin: auto;
}

[class*="container2-profile"]{
    height: 100%;
    width: 100%;

    margin: auto;
    border: 10px solid deeppink;
}

.container2-profile-left{
    grid-column: 2;
    grid-row: 3;
}

.container2-profile-right{
    grid-column: 4;
    grid-row: 3;
}



.profileBar{
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(5, 1fr);

    height: 90%;
    width: 90%;

    background: inherit;
}

#name{
    font-size: 40px;
    font-weight: bolder;
    font-family: "Bauhaus 93", sans-serif;
}

#desc{
    font-size: 20px;
    font-weight: bold;
    font-family: "Bauhaus 93", sans-serif;
}