

/*contacts*/

.container1-contact{
    width: 100vw;
    height: 20vh;
    margin: auto;

    border: 10px solid green;
}

.container2-contact{
    grid-column: 1;

    width: 25%;
    height: 25%;

    border: 10px solid green;
}



.contactBar{
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto auto auto;


    height: 90%;
    width: 90%;

    background: inherit;
}


#linkedin{
    max-height: 100%;
    max-width: 100%;
}

#github{
    max-height: 100%;
    max-width: 100%;
}

#discord{
    max-height: 100%;
    max-width: 100%;
}