/*navbar*/
/*move navbar to the right side and have it instead of the scrollbar*/
.navBar{
    display: grid;
    grid-template-columns: auto auto auto;

    width: 30%;

    margin: 50px auto auto;
    border-radius: 25px;
}

#navButton{
    width: 60%;
    height: 100%;

    margin: auto;
    border-radius: 25px;

    text-align: center;
    font-size: 25px;
    font-style: normal;
    font-weight: bold;

    background: #9198e5;
}
