/*projects*/

/*id should be used for a SINGLE ELEMENT
classes should be used for REPEATING ELEMENTS
*/


/* need to change the structure of the pages
the structure should be page -> container -> grid inside container -> container inside grid spaces -> elements inside containers
the containers would allow for easier variation of size on page without altering the major aspects of the grid or elements inside*/

#container1-projects{
    border: 10px solid red;
    width: 100vw;
    height: 200vh;
    margin: auto;
}
#container1-projects > h1{
    font-weight: bold;
    font-size: 50px;
    margin: auto;
    text-align: center;
}

[class*="container2-projects"] {
    /*base container properties*/
    width: 100%;
    height: 100%;
    margin: auto;
    border: 10px solid blue;
}

.container2-projects-vertical{
    grid-row: span 2;
}

.container2-projects-horizontal{
    grid-column: span 2;
}



.projectsBar{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-gap: 2.5%;


    margin-right: 10%;


    height: 90%;
    width: 90%;

    background: inherit;

}

.project{


    background: red;
    border: 5px solid blue;
    height: 100%;
    width: 100%;

    margin: auto;
}

#projName{
    font-size: 35px;
    font-weight: bold;
    margin: auto;
}

#projDescription{
    font-size: 25px;
}

